		title: 'Brighton',
		introText: 'Brighton (/bratn/) is a constituent part of the city of Brighton and Hove, a former town situated on the southern coast of England, in the county of East Sussex. It is best known as a seaside resort and is positioned 47 miles (76 km) south of London. It was created from the neighbouring but formerly separately governed towns of Brighton and Hove.',
