		id: '5',
		title: 'Edinburgh',
		introText: 'Edinburgh (/dnbr/ (About this soundlisten); Scots: Edinburgh; Scottish Gaelic: Dn ideann [tun etn]) is the capital of Scotland and one of its 32 council areas. Historically part of the county of Midlothian (interchangeably Edinburghshire before 1921), it is located in Lothian on the Firth of Forth\'s southern shore.',
