		content: `<p>The city is home to the University of Oxford, the oldest university in the English-speaking world, and has buildings in every style of English architecture from late Anglo-Saxon. Oxford's industries include motor manufacturing, education, publishing, information technology and science.</p><img src="${oxfordImg2}" alt="Bodleian Library"><p>Oxford was first settled by the Anglo-Saxons and was initially known as Oxnaford, meaning "ford of the oxen", as referenced in Florence of Worcester's Chronicon ex chronicis. A river crossing for oxen began around AD 900.</p><img src="${oxfordImg3}" alt="Quaking Bridge"><p>In the 10th century, Oxford became an important military frontier town between the kingdoms of Mercia and Wessex and was raided by Danes. In 1002, many Danes were killed in Oxford during the St. Brice's Day massacre ordered by thelred the Unready.[8] The skeletons of more than thirty suspected victims were unearthed in 2008 during the course of building work at St John's College. The massacre was a contributing factor to King Sweyn I of Denmarks invasion of England in 1003 and the sacking of Oxford by the Danes in 1004.</p>`,
